<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-form ref="registrationForm" v-model="valid" lazy-validation>
          <v-text-field
            v-model="userData.user_name"
            :counter="40"
            :rules="userNameRules"
            :label="$t('auth.user_name')"
            required
          ></v-text-field>
          <v-text-field
            v-model="userData.user_mail"
            :rules="emailRules"
            :label="$t('global.email_title')"
            required="true"
          ></v-text-field>
          <v-text-field
            v-model="userData.user_phone"
            :rules="phoneRules"
            :label="$t('global.phone_title')"
            required="true"
          ></v-text-field>
          <!-- v-progress-linear v-show="registrationInProgress" :indeterminate="true"></v-progress-linear-->
          <v-dialog
            v-model="registrationInProgress"
            persistent
            width="300"
          >
            <v-card
              color="primary"
              dark
            >
              <v-card-text>
                Please stand by
                <v-progress-linear
                  indeterminate
                  color="white"
                  class="mb-0"
                ></v-progress-linear>
              </v-card-text>
            </v-card>
          </v-dialog>
          <v-btn
            :disabled="!valid"
            @click="runRegistration"
            color="info"
            primary
          >
            {{ $t('auth.login_button_text') }}
          </v-btn>
          <v-btn @click="clear">
            {{ $t('global.clear_title') }}
          </v-btn>
        </v-form>
        <v-divider></v-divider>
        <v-card-text style="text-align: center">
          {{ $t('global.or_word') }}
        </v-card-text>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>
<script>
export default {
  components: {

  },
  data () {
    return {
      registrationInProgress: false,
      userData: {
        user_name: null,
        user_phone: null,
        user_another_phone: null,
        user_mail: null

      },
      currentUser: this.$store.state.AppStore.currentUser,
      valid: false,
      passwordRules: [
        v => !!v || this.$t('auth.user_password_required')
      ],
      userNameRules: [
        v => !!v || this.$t('auth.user_name_required'),
        v => /.+@.+/.test(v) || this.$t('login.user_name_must_be_valid')
      ],
      emailRules: [
        v => /.+@.+/.test(v) || this.$t('auth.user_email_must_be_valid')
      ],
      phoneRules: [
        v => !!v || this.$t('auth.user_phone_required'),
        v => /^[a-zA-Z0-9\-().\s]{10,15}$/.test(v) || this.$t('auth.user_phone_must_be_valid')
      ]
    }
  },
  created () {
    // this.$refs.registrationForm.validate()
  },
  methods: {
    runRegistration () {
      if (this.$refs.registrationForm.validate()) {
        this.registrationInProgress = true
        this.$store.dispatch('AppStore/register', {userData: this.userData}).then(() => {
          this.registrationInProgress = false
          let newUser = this.$store.state.AppStore.currentUser
          if (newUser && newUser.token) {
            this.$router.replace('/')
          } else {
            this.clear()
            this.$refs.registrationForm.validate()
          }
        })
      }
    },
    clear () {
      this.userData.user_name = null
      this.userData.user_password = null
    }
  }
}
</script>
