import userAPI from '@/services/InitService'
import PersistenceFactory from './persistence/PersistenceFactory.js'
// import respMock from '@/../mocks/app_mock'
const StorageManager = PersistenceFactory.StorageManager
const state = {
  currentLanguage: 'en',
  showSettings: false,
  currentUser: null,
  registerStatus: null
}
// getters
const getters = {
  userInfo: (state, getters, rootState) => {
    return state.currentUser
  },
  isSettingsShown: () => {
    return state.showSettings
  },
  getRegisterStatus: () => {
    return state.registerStatus
  }
}
// actions
const actions = {
  register ({ commit }, userData) {
    return userAPI.register(userData).then((result) => {
      if (result.status === 200) {
        commit('validateRegistration', result.data.user)
      } else {
        commit('setUser', null)
      }
    }).catch(() => {
      commit('setUser', null)
    })
  },
  login ({ commit }, userLogin) {
    return userAPI.login(userLogin).then((result) => {
      if (result.status === 200) {
        commit('setUser', result.data.user)
      } else {
        commit('setUser', null)
      }
    }).catch(() => {
      commit('setUser', null)
    })
  },
  logout ({ commit }) {
    commit('setUser', null)
  },
  setLanguage ({ commit }, lang) {
    commit('setCurrentLanguage', lang)
  },
  setShowSettings ({ commit }, isShown) {
    commit('setShowSettings', isShown)
  }
}
// mutations
const mutations = {
  validateRegistration (state, lang) {
    state.registerStatus = 1
  },
  setUser (state, user) {
    state.currentUser = user
    StorageManager.setItem('currentUser', state.currentUser)
  },
  setCurrentLanguage (state, lang) {
    state.currentLanguage = lang
  },
  setShowSettings (state, isShown) {
    state.showSettings = isShown
  },
  setAppInitData (state, appData) {
    // state.appData
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
